











































import { Component, Vue } from 'vue-property-decorator';
import Arm from './components/Arm.vue';
import Target from './components/Target.vue';
import TitleScreen from './components/overlays/TitleScreen.vue';
import GameOver from './components/overlays/GameOver.vue';
import * as Tone from 'tone';
import Capture from './components/Capture.vue';
import gsap from 'gsap';

@Component({
  components: {
    Arm,
    Target,
    TitleScreen,
    GameOver,
    Capture
  },
})
export default class App extends Vue {

  $refs!: {
    capture: HTMLElement,
    arm: Arm
  }

  bPaused = false;
  bPlaying = false;
  bTitle =  true;

  bGameOver = false;
  oPosition: {x: number, y: number} = {x: 0, y: 0};

  private mOutEvt(){
    this.bPaused = true;
  }

  private mInEvt(){
    this.bPaused = false;
  }

  private mMovEvt(e: any){
    this.oPosition = e;
  }

  public hitTarget(){
    const synth = new Tone.Synth().toDestination();
    synth.triggerAttackRelease("C4", "8n");
    Tone.start();
  }

  public start(){
    this.toneStart()
  }

  public async toneStart(){
    await Tone.start()
  }

  private onEnter(){
    gsap.from('#pause', {opacity: 0, duration: .3})
  }

  private onLeave(){
    this.step();
  }

  private step(){
    const step = 10;

    setTimeout(()=> {
      this.$refs.arm.update();
      window.requestAnimationFrame(this.step)
    }, step)
  }

  mounted(){
    this.$nextTick(()=>{
      // this.renderWatch();
      gsap.to('#title', {y: 0, duration: 1});
      window.requestAnimationFrame(this.step);
    });
  }

}
